@import 'function';
@import 'reset';
@import 'sp/_setting';
@import 'sp/_layout';
// plugin
@import 'slick';

strong {
  font-weight: bold;
}

.hidden {
  visibility: hidden;
}

.strong {
  font-weight: bold;
}

.subject {
  position: relative;
  padding: 20px 0;
}

.maillink {
  font-weight: bold;
  text-decoration: underline;
}

.button {
  width: 230px;
  height: 40px;
  line-height: 40px;
  background: #e2e2e3;
  text-align: center;
  margin: 0 auto;
	border: none;
	display: block;
  cursor: pointer;
  &.ora {
    background: $color-orange;
	  height: 40px;
	  line-height: 40px;
    color: #fff;
    a {
      color: #fff;
    }
  }
  &.small {
    width: 240px;
    color: #fff;
    height: 60px;
    line-height: 60px;
  }
  a {
    display: block;
    font-weight: bold;
    height:  100%;
  }
}


.button-link {
  width: 100%;
  height: 60px;
  line-height: 60px;
  text-align: center;
  border: 1px solid #f58f01;
  box-sizing: border-box;
  margin: 15px 0 0;
	display: block;
  a {
    display: block;
    font-weight: bold;
    height:  100%;
  }
}

.back-button {
}


// #top

#content .page-intro {
  position: fixed; 
  height: 100%;
  // height: 500px;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  text-align: center;
  color: $color-blue;
  background: #fff url(../img/pc/top/bg_intro.jpg) center bottom no-repeat;
  background-size: cover;

  .wrap {
    position: absolute;
    top: 50%;
    width: 100%;
    margin-top: -253px;
	  padding: 0 20px;
	  box-sizing: border-box;
  }
  .read {
    position: relative;
    padding: 100px 0 125px;
  }
  .arrow {
  }
}

#news {
  position: relative;
	padding-bottom: 20px;

	.news-media {
    position: absolute;
    right: 0;
    top: 100px;
    .news-media-wrapper {
      position: relative;
      overflow: hidden;
      width: 240px;
      height: 180px;
      img {
        width: 100%;
      }
    }

	}

	.news-ul {
		padding: 20px 0 0;
	}

	.news-li {
		overflow: hidden;
		margin: 0 0 10px;
		font-size: $font-size-normal;
	}

	.date {
		font-weight:  bold;
	}

	.subject {
		a {
			text-decoration: underline;
		}
	}
}


.page-top {
  opacity: 0;

  .h3 a {
	  color: #fff;
    font-weight: bold;
  }

  #main {
    padding-bottom: 30px;

    .slider-wrapper {
      position: relative;

	    .slick-dots {
		    padding: 15px 0 0;
		    text-align: center;
		    li {
			    &.slick-active {
				    background: $color-orange;
			    }
			    display: inline-block;
			    background: #ccc;
			    width: 10px;
			    height: 10px;
			    border-radius: 5px;
			    margin: 0 3px;
			    button {
				    opacity: 0;
			    }
		    }
	    }

    }

	  .banner-wrapper {
		  position: relative;
		  margin: 0 auto;
		  padding: 0 35px;
	  }

	  .banners {
		  display: table;
		  width: 100%;
		  .banner {
			  display: table-cell;
			  width: 50%;
			  p {
				  padding: 0 5px 0 0;
			  }
		  }
		  .banner2 {
			  display: table-cell;
			  width: 50%;
			  p {
				  padding: 0 0 0 5px;
			  }
		  }
	  }

    .banner3 {
      cursor: pointer;
    }

    .copy {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      height: 100%;
      width: 100%;
	    img {
		    display: block;
		    width: 90%;
		    margin: auto;
	    }
    }

    .read {
      position: absolute;
      top: 40%;
      width: 100%;
      text-align: center;
      color: $color-blue;
      font-size: $font-size-title1;
      margin-top: -25px;
	    img {
		    display: block;
		    width: 90%;
		    margin: auto;
	    }
    }

    .slider {
      position: relative;
      overflow: hidden;
    }

    .thumbs {
      position: absolute;
      overflow: hidden;
      bottom: -30px;
      right: 0;
      z-index: 10;
      .li {
        position: relative;
        float: left;
        width: 100px;
        height: 75px;
        cursor: pointer;
        &.active {
          .cover {
            opacity: 0;
          }
        }
        .cover {
          position: absolute;
          top: 0;
          width: 100%;
          height: 100%;
          background: #000;
          opacity: 0.4;
        }
      }
    }
  }


  #guide {
    .content-block {
      background: #fff;
      padding-bottom: 40px;
    }

    .h3 {
      height: 40px;
      line-height: 40px;
      background-color: $color-orange;
      color: $color-base;
      font-size: $font-size-title1;
      margin: 0 0 15px;
	    padding: 0  20px;
      img {
        vertical-align: middle;
        margin: 0 5px 2px 0;
	      width: 10px;
      }
    }
	  .htxt {
		  padding: 0 25px 0;
		  font-size: $font-size-normal;
		  line-height: 20px;
	  }
  }

  #life {
    .content-block {
      padding-bottom: 30px;
      background: #fff;
      &.up {
        overflow: hidden;
        padding: 0;
        height: 640px;
      }
    }
    .h3 {
      height: 40px;
      line-height: 40px;
      background-color: $color-orange;
      color: $color-base;
      font-size: $font-size-title1;
	    padding: 0 20px;
      img {
        vertical-align: middle;
        margin: 0 5px 2px 0;
	      width: 10px;
      }
    }

    .slider-wrapper {
	    position: relative;
      width: 100%;
      overflow: hidden;
      z-index: 12;
	    padding: 0 60px;
	    box-sizing: border-box;
      .inner {
        position: relative;
        margin: 0 auto;
	      padding: 0;
      }
      .sul {
        position: relative;
      }
      .al {
	      position: absolute;
	      top: 100px;
        left: 20px;
        z-index: 100;
      }
      .ar {
	      position: absolute;
	      z-index: 100;
	      top: 100px;
	      right: 20px;
      }
      .sul {
        overflow: hidden;
      }
      .sli {
	      img {
		      width: 100%;
	      }
        //width: 100%;
      }

	    .info {
		    padding: 10px 0 0;
		    overflow: hidden;
	    }

      .left {
        font-weight: bold;
      }
    }

    .key {
      padding: 20px 0 14px;
    }
    .arrow {
      vertical-align: middle;
      margin: 0 6px 2px ;
    }
  }

  #history {
    padding-bottom: 80px;
    .img-title {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
    }

    .left {
      float: left;
      width: 50%;
    }
    .right {
      float: left;
      width: 50%;
    }

    .h01 {
      padding: 60px 0 0 70px;
    }
    .h02 {
      padding: 20px 0 30px 20px;
    }
    .h03 {
      padding: 0 0 0 22px;
    }
    .txt {
      padding: 30px 0 0;
      line-height: 32px;
    }

  }

  #dream {

    padding-bottom: 80px;
    .img-title {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 10;
    }

    .left {
      float: left;
      width: 360px;
    }
    .right {
      float: left;
      width: 600px;
    }

    .d02 {
     padding: 30px 0 0 50px;
    }
    .d03 {
      padding: 90px 0 0;
    }
    .txt {
      padding: 25px 0 0 80px;
      line-height: 32px;
    }
  }

  #gakka {
    .content-block {
      background: #fff;
      padding-bottom: 40px;
    }
    .h3 {
      height: 40px;
      line-height: 40px;
      background-color: $color-orange;
      color: $color-base;
      font-size: $font-size-title1;
      margin: 0 0 15px;
	    padding: 0 20px;
      img {
        vertical-align: middle;
        margin: 0 5px 2px 0;
	      width: 10px;
      }
    }
    .ul {
      overflow: hidden;
      font-weight:  bold;
      text-align: center;
    }
  }

  #shiken {
    .content-block {
      background: #fff;
      padding-bottom: 40px;
    }

    .left {
	    padding: 10px 20px;
    }
	  .h3 {
      height: 40px;
      line-height: 40px;
      background-color: #ff9900;
      color: #fff;
		  padding: 0 20px;
      img {
        vertical-align: middle;
        margin: 0 5px 2px 0;
      }
    }

    .set {
      position: relative;
      overflow: hidden;
      top: -20px;
    }
    .s1 {
      position:relative;
      top: -50px;
      padding: 0 0 0 180px;
    }
    .s2 {
      top: 0;
      padding: 0 0 0 47px;
    }
    .s3 {
      position: absolute;
      top: 0;
      right: 0;
    }
    
    .openc {
      position: absolute;
      top: 0;
      left: 0;
    }

  }
}

.category-index {

  .content-block {
    background: #fff;
    padding-bottom: 20px;
  }

	.ul-first {
		.ul {
			display: table;
		}
		.li {
			display: table-cell;
			width: 50%;
			vertical-align: middle;
			float: none;
			img {
				vertical-align: top;
			}
			&.text {
				background: #143f80;
				a {
					color: #fff;
				}
			}
		}
		.txt {
			text-align: center;
			padding: 5px 0 0;
			font-weight: bold;
		}
	}

  .flow {
	  overflow: hidden;

    .flow-left  {
	    float: left;
      width: 550px;
      .title {
        padding: 0 0 5px;
        margin: 0;
        font-size: 18px;
        color: $color-blue;
      }

      .sub {
        margin: 0 0 15px;
        font-size: 14px;
        line-height: 24px
      }

    }

    .flow-right {
      float: right;
      font-weight: bold;
      text-align: center;
      .key {
        padding: 0px 0 10px;
      }
    }

  }

  .ul {
	  width: 100%;
    overflow: hidden;
    font-weight:  bold;
    text-align: center;
    box-sizing: border-box;
  }

  .li {
    float: left;
    width: 50%;
    //height: 170px;
	  .key img {
		  width: 100%;
	  }
    a {
	    display: block;
    }
    &.small {
      height: 96px;
      line-height: 96px;
    }

    &.middle {
	    width: 50%;
    }
    &.large {
      width: 50%;
    }
    &.home_schooling {
      width: 478px;
    }
    &.full {
      float: left;
      width: 958px;
      text-align: left;
      border-right: 2px solid $border-color;
    }
  }

  .wrap {
	  font-size: $font-size-normal;
  }

	.txt-wrap {
		display: table;
		height: 50px;
		width: 100%;
		.txt {
			display: table-cell;
			vertical-align: middle;
		}
	}

  .key {
    padding: 10px 0 5px;
  }

  .arrow {
    vertical-align: middle;
    margin: 0 6px 2px 0;
  }

  .label {
    display: table;
    height: 40px;
    width: 100%;
    text-align: center;
    font-size: 14px;
    line-height: 22px;
    .txt {
      text-align: center;
      vertical-align: middle;
      display: table-cell;
    }
  }

}




#exam {

  .top {

    .left {
      float: left;
      width: 320px;
    }

    .right {
      float: left;
      width: 630px;

      .wrap {
        padding: 20px 0 0 20px;
        line-height: 22px;
