          font-weight: normal;
        }
      }
    }

    .ico {
      padding: 5px 0 0 5px;
    }

    .note {
      padding: 8px 0;
    }
    .term {
      .date {
        background: $color-link-hover;
        color: #fff;
        width: 50px;
        height: 30px;
        line-height: 30px;
