  &.open {


    .icon {
      padding: 20px 0;
      text-align: center;
    }
    .infomation {
      position: relative;
      line-height: 22px;

      .cnt-detail {
        padding-top: 20px;
        padding-bottom: 0;
      }
    }
    .info-dt {
      padding: 20px 0 0;
      font-weight: bold;
    }
    .person-info {
      font-weight: normal;
    }

    .sh {
      .date {
        font-weight: bold;
        padding: 15px 0 10px;
      }

      .recruit-block {
        position: relative;
      }
      .rwrap {
        margin: 0 0 5px;
	      text-align: center;
      }
      .label {
        vertical-align: middle;
        font-weight: bold;
        height: 40px;
        line-height: 40px;
        background: $color-orange;
      }
      .value {
        vertical-align: middle;
	      padding: 10px;
      }

    }

    .program {
      position: relative;
      padding: 30px 0 50px;
      h4 {
        padding: 0 0 15px;
        font-weight: bold;
      }
      .ul {
        position: relative;
        border-left: 1px solid $color-blue;
        border-top: 1px solid $color-blue;
	      border-right: 1px solid $color-blue;
        overflow: hidden;
	      margin: 0 0 20px;
        .li {
	        width: 100%;
          border-bottom: 1px solid $color-blue;
          display: table;
	        height: 60px;
          background: #dae8ff;
          > p {
            display: table-cell;
            vertical-align: middle;
            text-align: center;
          }
        }
      }
      .note {
        padding: 0 0 20px;
      }

      .bold {
        font-weight: bold;
      }

    }

    .profile {
      position: relative;
      display: block;
      background: url(../img/pc/exam/open/arrow-right.png) right -10px center no-repeat;
	    background-size: 30px auto;
      height: 100%;
    }

    #profile {
      position: relative;
      display: none;
      padding: 0 0 20px;
	    .photo {
		    text-align: center;
		    padding: 10px 0;
	    }
    }
  }

  &.points, &.suisen {
    .h3 {
      font-weight: bold; 
    } 
  }

  &.points {
    .cnt-wrap {
      margin-bottom: 20px; 
    }
    table {
      margin: 10px 0; 
      line-height: 16px;
      th {
        width: 32.142857142857145%; 
      }
      td {
        text-align: left; 
      }
    }
    .section {
      padding: 0 0 20px 0!important; 
    }
    .note {
      line-height: 16px; 
    }
    .txt {
      line-height: 20px; 
    }
    .full {
      width: 100%;
    } 
    .ora {
      background-color: $color-orange; 
    }
    .center {
      text-align: center; 
    }
    .p0 {
      padding-bottom: 0 !important; 
    }
    .p10 {
      padding-bottom: 10px !important; 
    }
    .m0 {
      margin-bottom: 0 !important; 
    }
    .m5 {
      margin-bottom: 5px !important; 
    }
    .m10 {
      margin-bottom: 10px !important; 
    }
    .m20 {
      margin-bottom: 20px !important; 
    }
    .m40 {
      margin: 0 0 20px; 
    }
    .small {
      font-size: 10px; 
    }
    .cnt-above {
      .wrap {
        padding-bottom: 20px;
      } 
    }
    .youkous {
      width: 82.14285714285714%; 
      margin: 0 auto;
      .youkou {
        height: 40px;
        width: 100%;
        margin-bottom: 20px;
        line-height: 40px;
        text-align: center;
        color: #ffffff;
        font-size: 12px;
        background: $color-orange;
        &:last-child {
          margin-bottom: 10px; 
        }
      } 
    }
    .attention {
      margin-top: 50px; 
      padding: 10px; 
      border: 2px solid #143f80;
      color: #1b3c78;
    }
    .go-back {
      width: 82.14285714285714%; 
      margin: 0 auto 10px auto;
      height: 40px;
      background-color: #e2e2e3;
      line-height: 40px;
      font-weight: bold;
      text-align: center;
    }
  }
  &.suisen {
    table {
      th, td {
        width: 50%; 
        text-align: center;
      }
    }
  }
}

