body {
	min-width: 100%;
	font-size: $font-size-normal;
	line-height: 22px;
	font-family: $font-family-sansserif;
	-webkit-text-size-adjust: 100%;
	background: #f2f2f2;
}

.control-wrapper {
	position: relative;
	max-width: 1280px;
	margin: 0 auto;
	background: #fff;
}

a {
	&:hover {
		opacity: 0.7;
		text-decoration: underline;
	}
}

.read {
	line-height: 22px;
}

.non {
	padding: 0 !important;
	margin: 0 !important;
}

.error {
	color: red;
	padding: 0 0 20px;
}

.ured {
	background: $color-ured;
}

.ublue {
	background: $color-ublue;
}

.ugreen {
	background: $color-ugreen;
}

.txt-ured {
	color: $color-ured;
}

.txt-ublue {
	color: $color-ublue;
}

.txt-ugreen {
	color: $color-ugreen;
}

img {
	vertical-align: top;
	border: 0;
}

a {
	text-decoration: none;
	color: $color-link;
}

.hidden {
	visibility: hidden;
}

.title {
	font-weight: bold;
	font-size: $font-size-normal;
	margin: 0 0 10px;
}

h2.title {
	height: 40px;
	line-height: 40px;
	background: $color-orange;
	color: #fff;
	padding: 0 20px;
	&.blue {
		background: #143f80;
	}
	a {
		color: #fff;
	}
}

.page-title {
	position: absolute;
	width: 100%;
	top: 50%;
	z-index: 3;
	left: 0;
	margin-top: -120px;
	// top: 0;
	.content-block {
		padding-left: 50px;
	}
	.pname {
		position: relative;
		display: table;
		height: 200px;
		width: 342px;
		border: 2px solid #fff;
		color: #fff;
		font-size: 24px;
		text-align: center;
		margin: 0 0 0 5px;

		span {
			font-size: 14px;
		}

		p {
			line-height: 30px;
			display: table-cell;
			vertical-align: middle;
		}

	}
}

#page-visual {
	overflow: hidden;
    padding-bottom: 20px;

	.visual {
		position: relative;
	}

	.left {
		float: left;
		width: 44%;
	}
	.right {
		float: left;
		width: 56%;
	}

	.page-visual-impact {
		position: absolute;
		right: 0;
	}

	.page-title {
	}

	.pop {
		position: relative;
	}

	.box {
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		z-index: 1;
		text-align: center;
		margin: -60px 0 0;
		&.no-icon {
			margin: -20px 0 0;
		}
		&.subdir {
			margin: -80px 0 0;
			.icon-title {
				padding: 8px 0 0;
				font-size: 21px;
				line-height: 1.3;
				span {
					font-size: 14px;
					font-weight: normal;
				}
			}
		}
	}

	.icon-title {
		padding: 8px 0 0;
		font-size: 21px;
		font-weight: bold;
	}
}

#page-name {
	text-align: center;
	.icon-title {
		span {
			font-weight: normal;
		}
		padding: 0 0 10px;
		font-weight: bold;
	}
}

#content {
	.intro {
		position: relative;
		padding: 25px 0 0;

		.right480 {
			text-align: center;
			margin: 20px auto;
		}
	}
}

.small-line {
	width: 50px;
	height: 3px;
	background-color: $color-link-hover;
}

.middle-line {
	width: 75px;
	height: 3px;
	background-color: $color-link-hover;
	margin: 0 auto;
	&.small {
		width: 30px;
	}
	&.no {
		margin: 0;
	}
	&.m10{
		margin: 0 0 10px;
	}
	&.m15{
		margin: 0 0 15px;
	}
	&.m20 {
		margin: 0 auto 20px;
	}
	&.m40 {
		margin: 0 0 40px;
	}
	&.m60 {
		margin: 0 0 60px;
	}
	&.m80 {
		margin: 0 0 80px;
	}
	&.m100 {
		margin: 0 0 100px;
	}
	&.blue {
		background: $color-blue;
	}
}

.wide-line-left {
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 60px;
	background-color: $color-link-hover;
}

.wide-line-right {
	position: absolute;
	top: 0;
	right: 0;
	width: 50%;
	height: 60px;
	background-color: $color-link-hover;
}

#content {
	position: relative;
	min-height: 1000px;
}

.wrapper {
	position: relative;
	padding: 0 20px 0 120px;
	transition: .5s;
	@include max-screen($break-point) {
		padding: 0 20px;
	}
	@include max-screen($break-point-sp) {
		padding-left: 20px;
		padding-right: 20px;
	}
}

#header {
	position: relative;
	padding: 0;
}

#nav {
	position: absolute;
	top: 0;
	right: 0;
	overflow: hidden;
	font-size: $font-size-small;
	.li {
		width: 100px;
		float: left;
		a {
			display: block;
			height: 40px;
			padding: 22px 0 0 10px;
			border-left: 2px solid $border-color;
			box-sizing: border-box;
		}
	}
}

#logo {
	position: relative;
	text-align: center;
	padding: 0 0 0 60px;
	margin: 0;
	background: url(../img/pc/koushou.png) left top no-repeat;
	background-size: 60px auto;
	@include max-screen($break-point-sp) {
		margin: 0 0 0 60px;
	}
	> a {
		display: block;
		text-align: center;
		img {
			height: 60px;
		}
		//padding: 0 0 0 30px;
	}
	.kousyou {
		position: absolute;
		top: 0;
		left: 0;
		font-size: 0;
		a {
			display: block;
			width: 60px;
			height: 60px;
		}
	}
	img {
		//margin: 0 120px 0 0;
	}
}

#menu-sp-icon {
	position: absolute;
	top: 0;
	left: 0;
	cursor: pointer;
	width: 60px;
	height: 60px;
	z-index: 10;
	background: #143f80 url(../img/pc/common/hamb.png) center center no-repeat;
	background-size: 75%;
	font-size: 0;
	display: none;

	@include max-screen($break-point-sp) {
		display: block;
	}
}

#menu-sp {
	position: absolute;
	width: 100%;
	top: 60px;
	left: 0;
	transition: left .5s;
	background: #fff;
	display: none;
	z-index: 100;
	padding-bottom: 10px;
	&.active {
		display: block;
	}

	.li {
		// border-bottom: #e2e2e3;
		text-align: left;
		border-bottom: 2px solid #143f80;
		font-size: 14px;

		&.on {
			background-position: left bottom !important;
			background-color: $color-link-hover;
			.box {
				color: #fff;
				border-top: 2px dotted $color-link-hover;
			}
			.category {
				display: block;
			}
		}

		&.shirou-li {
			margin: 10px auto 20px;
			border: none;
			width: 240px;
			text-align: center;
		}

		.box {
			position: relative;
			display: block;
			width: 100%;
			height: 44px;
			line-height: 44px;
			box-sizing: border-box;
			padding: 0 15px;
			background: url(../img/pc/common/plus.png) right 15px center no-repeat;
			background-size: 14px auto;

			&.active {
				background: $color-link-hover url(../img/pc/common/minus.png) right center no-repeat;
				background-size: 44px auto;
				color: #fff;
				height: 44px;
				line-height: 44px;
				margin: 2px 0;
			}
			&.shiryou {
				background: #143f80;
				color: #fff;
			}
			&.shiryou2 {
				height: 44px;
				line-height: 44px;
				background: #ff9900;
				color: #fff;
			}
		}
	}

	.category {
		background-color: #fde6c5;
		overflow: hidden;
		box-sizing: border-box;
		display: none;

		.wrap {
			position: relative;
			text-align: left;
		}

		.ttl {
			color: #ff9900;
			font-weight: bold;
			a {
				color: #ff9900;
			}
		}

		.sl {
			border-top: 2px solid #fff;
			height: 44px;
			line-height: 44px;
			box-sizing: border-box;
			padding: 0 15px;
			background: url(../img/pc/common/arrow.png) right 15px center no-repeat;
			background-size: 14px auto;
			a {
				display: block;
				height: 100%;
			}
		}

	}

	.icon {
		padding: 20px 0 0 0;
	}

}

#menu {
	position: absolute;
	width: 120px;
	z-index: 15;
	top: 0;
	left: 0;
	transition: left .5s;
	background: #fff;
	font-size: $font-size-menu;
	@include max-screen($break-point) {
		left: -100px;
	}
	@include max-screen($break-point-sp) {
		display: none;
	}
	&.start {
		left: 0;
	}
	a {
		&:hover {
			opacity: 1;
		}
	}

	.li {
		// border-bottom: #e2e2e3;
		text-align: center;

		&.on {
			background-position: left bottom !important;
			background-color: $color-link-hover;
			.box {
				color: #fff;
				border-top: 2px solid $color-link-hover;
				border-left: 2px solid $color-link-hover;
				border-right: 2px solid $color-link-hover;
			}
			.category {
				display: block;
			}
		}

		&.school {
			background: url(../img/pc/menu/school.png) left top no-repeat;
			.category {
				background: url(../img/pc/menu/bg_01.jpg) right bottom no-repeat;
			}
		}

		&.gakuen {
			background: url(../img/pc/menu/gakuen.png) left top no-repeat;
			.category {
				background: url(../img/pc/menu/bg_02.jpg) right bottom no-repeat;
			}
		}

		&.gakka {
			background: url(../img/pc/menu/gakka.png) left top no-repeat;
			.category {
				background: url(../img/pc/menu/bg_03.jpg) right bottom no-repeat;
			}
		}

		&.shiken {
			background: url(../img/pc/menu/shiken.png) left top no-repeat;
			.category {
				background: url(../img/pc/menu/bg_04.jpg) right bottom no-repeat;
			}
		}

		&.tsushin {
			.category {
				background: url(../img/pc/menu/bg_05.jpg) right bottom no-repeat;
			}
		}

		.box {
			position: relative;
			display: block;
			width: 120px;
			height: 120px;
			color: $color-text;
			border-top: 2px solid $border-color;
			border-left: 2px solid $border-color;
			border-right: 2px solid $border-color;
			box-sizing: border-box;
			&.shiryou {
				width: 120px;
				height: 120px;
				line-height: 120px;
				border: none;
				background: #143f80;
				color: $color-base;
			}
			&.small {
				height: 76px;
				.txt {
					bottom: 28px;
				}
			}

			&.small2r {
				height: 80px;
				line-height: 1.5;
				border-bottom: 2px solid $border-color;
				.txt {
					bottom: 18px;
				}
			}
		}
	}

	.category {
		position: absolute;
		left: 120px;
		top: 120px;
		background-color: #fff;
		height: 558px;
		overflow: hidden;
		box-sizing: border-box;
		border-top: 2px solid $border-color;
		border-right: 2px solid $border-color;
		border-bottom: 2px solid $border-color;
		display: none;
		width: 320px;

		.wrap {
			position: relative;
			text-align: left;
			padding: 30px 0 0 20px;
		}

		.ttl {
			color: #ff9900;
			font-size: 21px;
			font-weight: bold;
			a {
				color: #ff9900;
			}
		}

		.ul {
			padding: 20px 0 0;
		}
		.sl {
			margin: 0 0 10px 0;
			font-size: 14px;
			img {
				vertical-align: middle;
				margin: 0 0 2px;
			}
		}

	}

	.icon {
		padding: 20px 0 0 0;
	}

	.txt {
		position: absolute;
		width: 100%;
		bottom: 20px;
	}

}

#footer {
	background: $color-blue;
	color: $color-base;
	//font-size: $font-size-small;

	.ul {
		text-align: left;
		.li {
			box-sizing: border-box;
			vertical-align: middle;
			height: 48px;
			line-height: 48px;
			border-bottom: 1px dotted #fff;
		}
		.shokuin {
		}
		.shiryou {
		}
		.privacy {
			text-align: center;
		}

		.contact, .access {
			margin: 0 5px 0 0;
			border: none;
			display: inline-block;
			height: 30px;
			line-height: 30px;
		}

		.button {
			display: block;
			width: 90px;
			height: 30px;
			line-height: 30px;
			background: #fff;
			font-size: 12px;
			color: $color-blue;
			text-align: center;
		}
		a {
			color: $color-base;
		}
	}

	.info {
		padding: 0 0 10px;
		p {
			vertical-align: top;
		}
		.seiei {
			padding: 20px 0 15px;
		}
		.text {
			font-size: 11px;
		}
		.facebook {
			margin-right: 5px;
		}
	}

	.copyright {
		text-align: center;
		width: 100%;
		padding: 8px 0;
		img {
			vertical-align: middle;
		}
	}
}

.content-wrapper {
	position: relative;
	padding: 0 0 0 20px;
}

.base-wrap {
	position: relative;
	padding: 0 0 10px;
}

.content-block {
	position: relative;
	width: 100%;
	margin: 0 auto;
	z-index: 11;
}

.btnback {
	position: absolute;
	right: 20px;
	cursor: pointer;
	display: none;
}

.section {
	overflow: hidden;
}

.arrow-right {
	margin-right: 5px;
}

