
#blog {

	.blog-cnt {
		padding: 0 20px;
	}

	.blog-sidebar {
		padding: 40px 20px 0;
		.swrap {
			margin:  0 0 10px;
		}

		.ss-ul {
			position: relative;
			padding: 10px 0 0;
		}

		.ss-ulr {
			position: relative;
			padding: 10px 0 0;
			.ss-li {
				padding: 15px 0px;
				line-height: 1.5;
				border-bottom: 1px solid $border-color;
				height: auto;
			}
		}

		.ss-li {
			height: 40px;
			line-height: 40px;
			border-bottom: 1px solid $border-color;
		}

		.select-block {
			position: relative;
			select {
				border-radius: 0;
				appearance: none;
				display: block;
				height: 40px;
				width: 100%;
				padding: 0 0 0 10px;
				background: $border-color url(../img/pc/select-arrow.png) right center no-repeat;
				background-size: 20px auto;
			}
		}

	}


	&.index {

		.list {
			.li {
				position: relative;
				margin: 0 0 30px;
			}
		}
		.ssub {
			background: $color-orange;
			padding: 0 10px;
			margin: 0 0 10px;
		}

		.photo {
			text-align: center;
			margin: 10px 0;
			img {
			}
		}

		.desc-little {
			overflow: hidden;
			a {
				text-decoration: underline;
			}
		}
		.paginate {
			text-align: center;
			.page-numbers {
				&.current {
					background: $color-orange;
					color: #fff;
				}
				display: inline-block;
				width: 30px;
				height: 30px;
				line-height: 30px;
				text-align: center;
				box-sizing: border-box;
				border: 1px solid $color-orange;
				margin: 0 2px;
				font-size: 14px;
			}
		}
	}

	.dt {

		.blog-desc {
			p {
				padding: 5px 0;
			}

		}

		.images {
			position: relative;
			.prev {
				position: absolute;
				top: 50%;
				left: 10px;
				z-index: 10;
				margin-top: -25px;
			}
			.next {
				position: absolute;
				z-index: 10;
				top: 50%;
				right: 10px;
				margin-top: -25px;
			}
			.slider {
				margin: 30px auto;
				background: #000;
				width: 480px;
				height: 360px;
			}
		}

		.page {
			position: relative;
			border-top: 2px solid $border-color;
			margin: 20px 0 0;
			padding: 40px 0 0;
			text-align: center;
			> p {
				display: inline-block;
				vertical-align: middle;
				&:last-child {
					margin: 0 0 0 30px;
				}
				img {
					vertical-align: middle;
				}
			}
		}

	}

}


