@charset "utf-8";
// ================================================== base
// ------------------------------------------ base
$support-ie6: false;
$support-ie7: true;
$use-html5: true;
$prefix: -webkit-, -moz-, -ms-, -o-, '';


// ================================================== style

// ------------------------------------------ font-family

$font-family-sansserif: 'ヒラギノ角ゴ Pro W3','Hiragino Kaku Gothic Pro','メイリオ',Meiryo,'ＭＳ Ｐゴシック','MS PGothic',sans-serif;
$font-family-en: Helvetica,Arial, sans-serif;


// ------------------------------------------ font-size

$font-size-base: 16px;
$font-size-small: 10px;
$font-size-normal: 12px;
$font-size-menu: 14px;
$font-size-xsmall: 12px;
$font-size-read: 18px, 1.8;
$font-size-title1: 12px;
$font-size-title2: 24px, 1.6;
$font-size-title3: 28px, 1.6;
$font-size-calendar: 36px, 1.6;


// ------------------------------------------ color
$color-base: #fff; //background

$color-text: #000;
$color-text2: #fff;

$color-menu-link: #000;
$color-menu-hover: #fff;

$color-link: #000;
$color-link-hover: #ff9900;
$color-link-visited: #000;
$color-blue: #143f80;
$color-orange: $color-link-hover;

$color-ured: #eb5656;
$color-ublue: #67c1d3;
$color-ugreen: #75c58c;



$border-color: #e2e2e3;

// ------------------------------------------ color



$break-point: 1280px;

$break-point-sp: 1024px;
