
#form-wrapper {
	position: relative;
	margin: 0 auto;
	&.confirm {
		.h3 {
			height: auto;
			line-height: 1;
		}
	}

	&.complete {
		border-top: 1px solid $color-blue;
	}

	.form-section {
		display: block;
		border-top: 1px solid $color-blue;
		border-bottom: 1px solid $color-blue;
		&.first {
			border-bottom: none;

			.h3 {
				padding: 20px 0;
				height: auto;
				line-height: 1.5;
			}
			.inputstyle {
				border-bottom: none;
				padding: 0 0 10px;
			}
		}
	}

	.value-2-box {
		display: table;
		width: 100%;
		> p {
			display: table-cell;
			width: 50%;
		}
		.one {
			padding: 0 5px 0 0;
		}
		.two {
			padding: 0 0 0 5px;
		}
		input {
			width: 100% !important;
		}
	}

	.inputstyle {
		padding: 10px 0;
		overflow: hidden;
	}

	.label {
		.note {
			padding: 10px 0;
		}
	}

	.notice {
		font-size: $font-size-small;
		line-height: 18px;
		margin: 0 0 10px;
	}

	.h2 {
		font-weight: bold;
		margin: 0 0 15px;
	}

	.checkblock {
		border: 1px solid $border-color;
		padding: 10px;
		width: 100%;
		box-sizing: border-box;
		margin: 0 0 10px;
		.row {
			margin: 0 0 10px;
		}
	}

	.table {
		width: 100%;
		vertical-align: middle;
		margin: 0 0 15px !important;
		.cell {
			width: 50px;
			vertical-align: middle;
			display: table-cell;
		}
		.text {
			width: 90%;
			vertical-align: middle;
			text-align: center;
		}
	}


	.value-block {
		display: inline-block;
		margin: 0 5px 0 0;
	}

	.h3 {
		font-weight: bold;
		height: 40px;
		line-height: 40px;
	}

	.input {
		display: inline-block;
		vertical-align: middle;
		padding: 0 0 0 20px;
		height: 50px;
		box-sizing: border-box;
		border: 2px solid $border-color;
		border-radius: 4px;
		width: 100%;
		&.small {
			width: 100px;
		}
		&.middle {
			width: 150px;
		}
		&.full {
			width: 100%;
		}
		&.m10 {
			margin: 0 10px 0 0;
		}
	}
	.textarea {
		width: 100%;
		height: 200px;
		box-sizing: border-box;
		border: 2px solid $border-color;
		border-radius: 4px;
		padding: 10px 20px;
	}

	.box {
		position: relative;
		margin: 0 0 15px;
		text-align: left;
	}

	.radio-block {
		padding: 10px 0;
		.radio {
			display: inline-block;
			margin: 0 10px 0 0;
		}
	}

	.select-num {
		position: relative;
		display: inline-block;
		margin: 0 0 0 10px;
		select {
			appearance: none;
			display: block;
			height: 50px;
			width: 80px;
			padding: 0 0 0 10px;
			background: $border-color url(../img/pc/select-arrow.png) right center no-repeat;
			background-size: 20px auto;
		}
	}

	.select-block {
		position: relative;

		&.oneline {
			display: inline-block;
			margin: 0 0 0 20px;
		}

		select {
			appearance: none;
			display: block;
			height: 50px;
			width: 200px;
			padding: 0 0 0 10px;
			background: $border-color url(../img/pc/select-arrow.png) right center no-repeat;
			background-size: 20px auto;
		}
	}

	.require {
		display: inline-block;
		width: 50px;
		height: 20px;
		line-height: 20px;
		text-align: center;
		color: #fff;
		background: $color-link-hover;
		vertical-align: middle;
		margin: 0 0 4px 2px;
		font-size: 12px;
		border-radius: 4px;
	}

	.small {
		display: inline-block;
		font-weight: normal;
		padding: 0 0 0 10px;
	}

	.value-box {
	}

	.buttons {
		padding: 20px 0 0;
		font-weight: bold;
		.buttons {
		}
		.last {
			margin-top: 10px;
		}
	}

}